<template>
  <div class="d-flex align-items-center justify-content-center vh-100">
    <div class="col-md-6">
      <div class="card">
        <div class="card-header">
          <h5 class="card-title">Onay Kodu</h5>
        </div>
        <div class="card-body">
          <h5 class="col-md-12 card-title fs-1" v-if="$store.state.code === true">{{ $store.state.codeText }}</h5>
          <h5 class="col-md-12 card-title" v-else>Onay Kodu Bekleniyor...</h5>
        </div>
        <div class="card-footer"></div>
      </div>
    </div>
  </div>
</template>

<style scoped>
.error {
  color: red;
}
</style>

<script>
import axios from "axios";
import { mapMutations } from "vuex";
import Pusher from "pusher-js";

export default {
  name: "OnayKodu",
  data() {
    return {
      code: this.$store.state.code,
      codeText: this.$store.state.codeText,
    };
  },
  async created() {
    let url = "/api/ajax/login";
    fetch(url, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    })
      .then(async (response) => {
        if (!response.ok) {
          return [];
        } else {
          let result = await response.json();

          if (result.success) {
            const pusher = new Pusher(result.data.pusher.key, {
              cluster: "mt1", // Replace with your cluster (e.g., 'us2')
              encrypted: true, // Use SSL
              authEndpoint: "https://app.posentegra.com/api/broadcasting/auth",
            });
            console.log(result);
            const channel = pusher.subscribe(`private-trigger.${this.$store.state.userId}`);
            console.log(`private-trigger.${this.$store.state.userId}`);
            // Bind to events on the channel
            channel.bind("Trigger", this.connect);
          } else {
            this.$store.state.login = false;
            this.$router.push("/login");
          }
        }
      })
      .catch(() => {});
  },
  methods: {
    ...mapMutations(["connect"]),
  },
};
</script>
