<template>
  <div v-show="this.$store.state.layout === 'landing' && this.$store.state.login" class="landing-bg h-100 bg-gradient-primary position-fixed w-100"></div>

  <main class="main-content position-relative max-height-vh-100 h-100 border-radius-lg">
    <!-- nav -->

    <router-view></router-view>
  </main>
</template>

<script>
import NavBar from "@/components/Navbars/NavBar.vue";
import OffcanvasMenu from "@/components/OffcanvasMenu.vue";
import { mapMutations } from "vuex";

export default {
  name: "App",
  components: {
    NavBar,
    OffcanvasMenu,
  },
  data: () => ({
    login: false,
    loading: true,
    providers: [],
  }),
  computed: {
    navClasses() {
      console.log(this.$store.state.isNavFixed, this.$store.state.darkMode);
      return {
        "position-fixed w-100 bg-white text-gray-800 left-auto  z-index-sticky": this.$store.state.isNavFixed && !this.$store.state.darkMode,
        "position-fixed w-100 bg-default left-auto   z-index-sticky": this.$store.state.isNavFixed && this.$store.state.darkMode,
        "position-absolute z-index-sticky  w-100  z-index-2": this.$store.state.isAbsolute,
      };
    },
  },
  beforeMount() {
    this.$store.state.isTransparent = "bg-transparent";
  },
  created() {
    // Get the darkMode flag from session storage
    const isDarkModeOn = window.localStorage.getItem("darkMode") === "true";

    // Set the isDarkMode data property to the value from session storage
    this.$store.state.darkMode = isDarkModeOn;
  },
  async mounted() {
    let url = "/api/ajax/login";
    fetch(url, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    })
      .then(async (response) => {
        if (!response.ok) {
          return [];
        } else {
          let result = await response.json();

          if (result.success) {
            this.$store.state.login = true;
            this.$store.state.accountName = result.data.pos.name;
            this.$store.state.token = result.data.token;
            this.$store.state.restaurantCount = Object.keys(result.data.restaurants).length;
            this.$store.state.userId = result.data.userId;

            await this.listOrders();
          } else {
            this.$store.state.login = false;
            this.$router.push("/login");
          }
        }
      })
      .catch(() => {});
  },
  methods: {
    ...mapMutations(["navbarMinimize"]),
    refreshList() {
      let url = "/api/ajax/getProviders";
      fetch(url, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      }).then(async (response) => {
        const res = await response.json();
        console.log(res);
        if (res.success) {
          this.loading = false;
          this.providers = res.data.restaurants;
        }
      });
    },
    toggleConfigurator(showConfig) {
      this.$store.state.showConfig = !showConfig;
      this.loading = true;
      if (!showConfig) {
        let url = "/api/ajax/getProviders";
        fetch(url, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
          },
        }).then(async (response) => {
          const res = await response.json();
          if (res.success) {
            this.loading = false;
            this.providers = res.data.restaurants;
          }
        });
      }
    },
    async listOrders() {
      return [];
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.offcanvas.show {
  transform: none;
  visibility: visible !important;
}
</style>
