<template>
  <div class="p-0 card-body" style="overflow-y: auto">
    <ul class="list-group m-0">
      <li class="list-group-item m-0">
        <div class="mb-3">
          <h6 class="mb-0">{{ option.restaurantName }}</h6>
        </div>
        <ul class="list-group">
          <li
            class="list-group-item m-0"
            v-for="subOption in option.options"
            :key="subOption.providerId"
          >
            <div class="mb-3">
              <h6 class="mb-0">{{ subOption.name }}</h6>
            </div>
            <ul class="list-group">
              <li>
                <div>
                  <div class="mt-3 text-left d-flex" style="text-align: left">
                    <div>
                      <h6 class="mb-0">Restoran Durumu</h6>
                      <p
                        class="text-sm w-100"
                        :class="
                          subOption.status ? 'text-success' : 'text-danger'
                        "
                      >
                        <span v-if="!loading[subOption.slug]['status']">
                          {{ subOption.status ? "Açık" : "Kapalı" }}
                        </span>
                        <span v-else-if="loading[subOption.slug]['status']">
                          Bekleyiniz...
                        </span>
                      </p>
                    </div>

                    <div class="form-check form-switch ps-0 ms-auto my-auto">
                      <input
                        class="mt-1 ms-auto form-check-input"
                        type="checkbox"
                        id="navbarFixed"
                        :checked="subOption.status"
                        @click="
                          updateRestaurant(
                            !subOption.status,
                            subOption.providerId,
                            'status',
                            subOption.slug
                          )
                        "
                      />
                    </div>
                  </div>
                </div>
              </li>
              <li>
                <div>
                  <div class="mt-3 text-left d-flex" style="text-align: left">
                    <div>
                      <h6 class="mb-0">Otomatik Onay</h6>
                      <p
                        class="text-sm w-100"
                        :class="
                          subOption.otomatikOnay
                            ? 'text-success'
                            : 'text-danger'
                        "
                      >
                        <span v-if="!loading[subOption.slug]['otomatikOnay']">
                          {{ subOption.otomatikOnay ? "Açık" : "Kapalı" }}
                        </span>
                        <span
                          v-else-if="loading[subOption.slug]['otomatikOnay']"
                        >
                          Bekleyiniz...
                        </span>
                      </p>
                    </div>

                    <div class="form-check form-switch ps-0 ms-auto my-auto">
                      <input
                        class="mt-1 ms-auto form-check-input"
                        type="checkbox"
                        id="navbarFixed"
                        :checked="subOption.otomatikOnay"
                        @click="
                          updateRestaurant(
                            !subOption.otomatikOnay,
                            subOption.providerId,
                            'otomatikOnay',
                            subOption.slug
                          )
                        "
                      />
                    </div>
                  </div>
                </div>
              </li>
              <li v-if="subOption.slug == 'ty'">
                <div>
                  <div class="mt-3 text-left d-flex" style="text-align: left">
                    <div>
                      <h6 class="mb-0">Servis</h6>
                      <p class="text-sm w-100">
                        <span v-if="!loading[subOption.slug]['service']">
                          {{ subOption.service }} dk
                        </span>
                        <span v-else-if="loading[subOption.slug]['service']">
                          Bekleyiniz...
                        </span>
                      </p>
                    </div>

                    <div class="ps-0 ms-auto my-auto">
                      <select class="form-select">
                        <option
                          v-for="min in minutes"
                          :key="min"
                          :selected="min == subOption.service"
                          :value="min"
                          v-on:change="
                            updateRestaurant(
                              subOption.service,
                              subOption.providerId,
                              'service',
                              subOption.slug
                            )
                          "
                        >
                          {{ min }}
                        </option>
                      </select>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </li>
    </ul>
    <!-- Navbar Fixed -->
    <!-- Navbar Fixed -->

    <hr class="horizontal dark my-2" />
  </div>
</template>
<style scoped>
.form-switch .form-check-input:checked {
  border-color: #10b36d;
  background-color: #0c8d56;
}
ul li select {
  display: inherit !important;
  width: 90px;
  padding: 15px;
  text-align: center;
  font-size: 25px;
}
</style>
<script>
export default {
  name: "OrderList",
  props: {
    option: {
      type: Object,
      required: true,
    },
    refreshList: {
      type: Function,
    },
  },

  data() {
    return {
      service: 30,
      minutes: [20, 35, 30, 35, 40, 45],
      loading: {
        webentegra: {
          otomatikOnay: false,
          status: false,
        },
        ys: {
          otomatikOnay: false,
          status: false,
        },
        ty: {
          otomatikOnay: false,
          status: false,
        },
        migros: {
          otomatikOnay: false,
          status: false,
        },
        getir: {
          otomatikOnay: false,
          status: false,
        },
        getirv2: {
          otomatikOnay: false,
          status: false,
        },
        getirV2: {
          otomatikOnay: false,
          status: false,
        },
      },
    };
  },
  methods: {
    updateRestaurant(on, provider, field, slug) {
      console.log(on, provider, field, "a");
      this.loading[slug][field] = true;
      fetch("/api/ajax/updateRestaurantStatus", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        body: JSON.stringify({
          field,
          on,
          provider,
        }),
      }).then(async (response) => {
        const res = await response.json();
        console.log(res, "res");
        if (res.success) {
          await this.refreshList();
          if (slug == "ys") {
            setTimeout(async () => {
              await this.refreshList();
            }, 5000);
          } else {
            setTimeout(async () => {
              await this.refreshList();
              this.loading[slug][field] = false;
            }, 2000);
          }
        }
        console.log(res);
      });
    },
  },
};
</script>

