<template>
  <div v-show="this.$store.state.login" class="py-6 container-fluid">
    <div class="row">
      <div class="col-12">
        <div class="card card-white text-white">
          <div class="card-body">Hoşgeldiniz {{ this.$store.state.accountName }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TableList",
  components: {},
  data() {
    return {
      data: [],
      count: this.$store.state.restaurantCount,
      stats: {},
    };
  },
  created() {
    // Get the darkMode flag from session storage
    const isDarkModeOn = window.localStorage.getItem("darkMode") === "true";

    // Set the isDarkMode data property to the value from session storage
    this.$store.state.darkMode = isDarkModeOn;
  },
  async mounted() {
    let result = await fetch("/api/ajax/restaurant", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    if (result) {
      this.data = await result.json();
    }
  },
};
</script>
